.main-header-container {
    display: flex;
    align-items: center;
    background-color: var(--primary-color);
    padding: 16px 24px;
    gap: 16px;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.288);
}

.mc-logo-image {
    height: 28px;
}

.header-link {
    height: 28px;
}