/* Fonts */

/* Mark for MC */

/* Light*/
@font-face {
  font-family: "MarkforMC";
  src: local("MarkforMc-Light"),
    url("./assets/fonts/normal/MarkForMC-Lt.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
}

/* Regular */
@font-face {
  font-family: "MarkforMC";
  src: local("MarkforMc-Regular"),
    url("./assets/fonts/normal/MarkForMC.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}

/* Medium */
@font-face {
  font-family: "MarkforMC";
  src: local("MarkforMc-Regular"),
    url("./assets/fonts/normal/MarkForMC.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}

/* Bold */
