/* Typography */

caption {
  font-family: "MarkforMC";
  font-weight: 400;
  font-size: 10px;
}

label {
  font-family: "MarkforMC";
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
}

body { 
  font-family: "MarkforMC";
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
}

.body-medium {
  font-family: "MarkforMC";
  font-weight: 500;
  font-size: 14px;
}

.body-bold {
  font-family: "MarkforMC";
  font-weight: 600;
  font-size: 14px;
}

.subheading {
  font-family: "MarkforMC";
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}

.subheading-medium {
  font-family: "MarkforMC";
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
}
.subheading-bold {
  font-family: "MarkforMC";
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
}

button {
  font-family: "MarkforMC";
  font-weight: 600;
}

.button-large {
  font-size: 16px;
  font-family: "MarkforMC";
  font-weight: 600;
}

.button-regular {
  font-size: 14px;
  font-family: "MarkforMC";
  font-weight: 600;
}

.button-small {
  font-size: 12px;
  font-family: "MarkforMC";
  font-weight: 600;
}

input {
  font-family: "MarkforMC";
  border-radius: 0; /* Reset border-radius */
  -webkit-appearance: none; /* Reset Safari's default styles */
  appearance: none;
}

.heading {
  font-family: "MarkforMC";
  font-weight: 400;
  font-size: 20px;
  color: var(--primary-text);
  line-height: 30px;
}

.heading-bold {
  font-family: "MarkforMC";
  font-weight: 600;
  font-size: 20px;
  color: var(--primary-text);
  line-height: 30px;
}

.heading-medium {
  font-family: "MarkforMC";
  font-weight: 500;
  font-size: 20px;
  color: var(--primary-text);
  line-height: 30px;
}

.title {
  font-family: "MarkforMC";
  font-weight: 400;
  font-size: 24px;

  line-height: 36px;
}

.title-medium {
  font-family: "MarkforMC";
  font-weight: 500;
  font-size: 24px;
  line-height: 36px;
}

.headline {
  font-family: "MarkforMC";
  font-weight: 500;
  font-size: 28px;
}

.headline-light {
  font-family: "MarkforMC";
  font-weight: 300;
  font-size: 28px;
}


.App {
display: flex;
flex-direction: column;
}


:root {

 /* Primary */
 --primary-color: #006B4B;

 /* Surface */
 --primary-surface: #ffffff;
 --secondary-surface: #f8f8f8;
 --tertiary-surface: #f1f1f1;
 --highlight-surface: #006B4B;
 --black-surface: #212121;

 /* Dividers */
 --divider-on-primary: #e1e1e1;
 --divider-on-secondary: #c8c8c8;

 /* Disabled */
 --disabled-state: #acacac;

 /* Icons */
 --icons-grey: #919191;
 --icons-primary: #006B4B;
 --icons-white: #ffffff;
 --icons-black: #141414;

 /* Text */
 --primary-text: #212121;
 --secondary-text: #6e6e6e;
 --white-text: #ffffff;
 --highlight-text: #006B4B;
 --button-text: #ffffff;

 /* Neutral colors */
 --black: #000000;
 --grey-900: #212121;
 --grey-500: #6e6e6e;
 --grey-400: #919191;
 --grey-300: #acacac;
 --grey-200: #c8c8c8;
 --grey-100: #e1e1e1;
 --grey-50: #f1f1f1;
 --grey-25: #f8f8f8;
 --white: #ffffff;

 /* Message */
 --success-text: #097007;
 --warning-text: #332805;
 --error-text: #b00606;

 --success-container: #dff7df;
 --warning-container: #fff4d1;
 --error-container: #fadede;

 --success-icon: #097007;
 --warning-icon: #ffc61e;
 --error-icon: #ee0000;
}

.white {
  color: var(--white);
}

.primary {
  color: var(--primary-color);
}

 /* Landing Page */
.mweb-container {
  display: flex;
  flex-direction: column;
  padding: 24px;
  height: 100vh;
  overflow: auto;
  gap: 16px;
  background-color: #141414;
  color: white;
  font-size: 16px;
  text-align: center;
}

.mweb-info-container {
  display: flex;
  flex-direction: column;
  gap: 16px;
  line-height: 21px;
  margin: 16px;
}

.mweb-info-container2 {
  display: flex;
  flex-direction: column;
  gap: 8px;
  line-height: 21px;
}



.share-icon-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
}

.share-icon-text-container {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 8px;
}

.share-icon-text-inner-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-bottom: 8px;
}

@keyframes moveUpDown {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-16px); 
  }
}

.dive {
  animation: moveUpDown 2s ease-in-out infinite; 
}



.icon16 {
  height: 16px;
}

.icon24 {
  height: 24px;
}

.page-content {
  padding-top: 56px;
}

.page-animated {
  animation: appSwitchIn 0.4s ease both;
}

@keyframes appSwitchIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
